import { getCurrentUser } from '@nextcloud/auth';
import Configure from '../mixins/Configure';
export default {
  name: 'Configuration',
  mixins: [Configure],
  props: {
    rootTitle: {
      type: String,
      required: true
    },
    loading: {
      type: Boolean,
      required: true
    }
  },
  data: function data() {
    return {
      isAdmin: getCurrentUser() === null ? false : getCurrentUser().isAdmin
    };
  }
};